*,
*::before,
*::after {
  box-sizing: border-box;
}
.page-header {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  padding-top: 6px;
}
.navigation {
  display: none;
}
@media screen and (min-width: $desktop) {
  .navigation {
    display: flex;
  }
}

.page-header__logo {
  width: 199px;
  height: 50px;
  display: inline-block;
  animation: zoomIn;
  -webkit-animation: zoomIn;
  animation-duration: 2s;
  animation-timing-function: linear;
  animation-iteration-count: 1;
}

@media screen and (min-width: $desktop) {
  .page-header__logo {
    position: absolute;
    top: 37px;
    left: -66px;
  }
}

@media screen and (min-width: $tablet) {
  .page-header__nav {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 37px;
  }
}

//Menu container

.menu__container {
  position: fixed;
  width: 100%;
  top: 0;
  display: block;
  padding: 20px;
  box-sizing: border-box;
  height: 100vh;
  transition: left 0.2s;
  z-index: 2;
  left: -100%;
  background-color: $burger-menu-bg-color;
  -webkit-transition: left 0.2s;
  -moz-transition: left 0.2s;
  -ms-transition: left 0.2s;
  -o-transition: left 0.2s;
  overflow-x: hidden;
}

@media screen and (min-width: $tablet) {
  .menu__container {
    position: fixed;
    width: 254px;
    top: 0;
    left: -254px;
    transition: left 0.2s;
    -webkit-transition: left 0.2s;
    -moz-transition: left 0.2s;
    -ms-transition: left 0.2s;
    -o-transition: left 0.2s;
    overflow: hidden;
  }
}

@media screen and (min-width: $desktop) {
  .menu__container {
    display: none;
  }
}

.menu__container--ticker {
  display: none;
}
.menu_btn {
  background-color: transparent;
  padding: 5px;
  position: absolute;
  top: 20px;
  right: 21px;
  cursor: pointer;
  transition: left 0.25s;
  z-index: 3;
  width: 25px;
  overflow: hidden;

  -webkit-transition: left 0.25s;
  -moz-transition: left 0.25s;
  -ms-transition: left 0.25s;
  -o-transition: left 0.25s;
}

@media screen and (min-width: $tablet) {
  .menu_btn {
    position: absolute;
    top: 52px;
    right: 180px;
  }
}

@media screen and (min-width: $desktop) {
  .menu_btn {
    display: none;
  }
}

@media screen and (max-width: 1199px) {
  .menu_btn span {
    display: block;
    height: 2px;
    background-color: $accent-color;
    margin: 3px 0 0;
    transition: all 0.1s linear 0.25s;
    position: relative;
    -webkit-transition: all 0.1s linear 0.25s;
    -moz-transition: all 0.1s linear 0.25s;
    -ms-transition: all 0.1s linear 0.25s;
    -o-transition: all 0.1s linear 0.25s;
  }
}

.menu_btn span.first {
  margin-top: 0;
}

.menu__container--ticker:checked ~ .menu_btn {
  right: 20px;
}

@media screen and (min-width: $tablet) {
  .menu__container--ticker:checked ~ .menu_btn {
    left: 210px;
    top: 15px;
    position: fixed;
  }
}

.menu__container--ticker:checked ~ .menu__container {
  left: 0;
}

.menu__container--ticker:checked ~ .menu_btn span.first {
  transform: rotate(45deg);
  top: 5px;
}

.menu__container--ticker:checked ~ .menu_btn span.second {
  opacity: 0;
}

.menu__container--ticker:checked ~ .menu_btn span.third {
  transform: rotate(-45deg);
  top: -5px;
}

.button__menu--buy {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 11px 48.64px 12px 48.64px;
  border: none;
  border-radius: 22px;
  color: $accent-color;
  background-color: $white-bg-color;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.3;
  animation: pulse;
  -webkit-animation: pulse;
  animation-duration: 2s;
  animation-timing-function: linear;
  animation-iteration-count: 5;
  cursor: pointer;
}

.button__menu--container {
  margin: 23px auto 32px auto;
}

@media screen and (max-width: $max-mobile) {
  .button__menu--header {
    display: none;
  }
}

@media screen and (min-width: $tablet) {
  .button__menu--header {
    display: flex;
    padding-left: 37.23px;
    padding-right: 37.23px;
  }
}
@media screen and (min-width: $tablet) {
  .button__menu--header:hover,
  .button__menu--header:focus {
    background-color: $accent-color;
    color: $text-white-color;
    outline: transparent;
  }
}

.button__menu--icon {
  margin-left: 15px;
  fill: $accent-color;
}
@media screen and (min-width: $tablet) {
  .button__menu--header:focus + .button__menu--icon {
    fill: $text-white-color;
  }
}

@media screen and (min-width: $desktop) {
  .page-header__list {
    display: flex;
    align-items: center;
    padding-left: 80px;
  }
}

.page-header__item {
  margin-bottom: 22px;
  border-bottom: 1px solid;
  color: $burger-menu-line-color;
}

@media screen and (min-width: $desktop) {
  .page-header__item {
    margin-bottom: 0;
    border-bottom: none;
  }
}
@media screen and (min-width: $desktop) {
  .page-header__item:not(:last-child) {
    margin-right: 35px;
  }
}

.page-header__link {
  display: block;
  color: $text-white-color;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.3;

  &.current {
    color: $accent-color;
  }
}
.page-header__link:hover {
  color: $accent-color;
  font-family: $title-font-family;
  font-size: 16px;
  letter-spacing: 2px;
  line-height: 1.3;
}

@media screen and (min-width: $desktop) {
  .page-header__link {
    font-size: 16px;
  }
}

//Hero

.hero {
  position: relative;
  min-width: 320px;
  width: 100%;
  min-height: 502px;
  margin-right: auto;
  margin-left: auto;
  background-color: $hero-header-main-bg-color;
}

@media screen and (min-width: $tablet) {
  .hero {
    min-height: 399px;
  }
}
@media screen and (min-width: $desktop) {
  .hero {
    min-height: 665px;
  }
}

.hero-images {
  position: relative;
  min-height: 399px;
}
@media screen and (min-width: $desktop) {
  .hero-images {
    min-height: 665px;
  }
}

// Hero-title

.hero__title {
  position: absolute;
  z-index: 1;
  top: 193px;
  width: 218px;
  font-family: $title-font-family;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 26px;
  line-height: 1.14;
  letter-spacing: 0.04em;
  color: $text-white-color;
}
.hero__title:hover {
  animation: bounce 1s linear;
  -webkit-animation: bounce 1s linear;
}

@media screen and (min-width: $tablet) {
  .hero__title {
    width: 162px;
    top: 102px;
    font-size: 22px;
  }
}

@media screen and (min-width: $desktop) {
  .hero__title {
    top: 171px;
    width: 277px;
    font-size: 38px;
  }
}

.hero__title span {
  display: block;
  font-family: $title-font-family;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 48px;
  line-height: 1;
  color: $contacts-box-shadow-color;
  color: $accent-color;
}

@media screen and (min-width: $tablet) {
  .hero__title span {
    font-size: 34px;
  }
}

@media screen and (min-width: $desktop) {
  .hero__title span {
    font-size: 58px;
    line-height: 1.1;
    letter-spacing: 0.04em;
  }
}

// Hero-button

.hero__button {
  position: absolute;
  top: 318px;
  z-index: 1;
  font-family: $main-font-family;
  font-weight: 400;
  font-size: 8px;
  line-height: 1.3;
}

@media screen and (min-width: $tablet) {
  .hero__button {
    top: 202px;
  }
}
@media screen and (min-width: $desktop) {
  .hero__button {
    top: 337px;
    font-size: 14px;
  }
}

@media screen and (min-width: $desktop) {
  .hero__button--products,
  .hero__button--mading {
    padding: 15px 20px;
  }
}

.hero__button--products {
  color: $text-white-color;
  background-color: $accent-color;
  display: inline-block;
  padding: 6px 15px;
  border: none;
  border-radius: 22px;
  margin-right: 8px;
