.hero__button--products:hover,
.hero__button--products:focus {
  background-color: $text-white-color;
  color: $accent-color;
}

@media screen and (min-width: $desktop) {
  .hero__button--products {
    padding: 15px 20px;
  }
}
.hero__button--mading {
  color: $accent-color;
  background-color: $text-white-color;
  display: inline-block;
  padding: 6px 14px;
  border: none;
  border-radius: 22px;
}

.hero__button--mading:hover,
.hero__button--mading:focus {
  background-color: $accent-color;
  color: $text-white-color;
}

@media screen and (min-width: $desktop) {
  .hero__button--mading {
    padding: 15px 20px;
  }
}

.hero__data,
.hero__information {
  display: none;
}

@media screen and (min-width: $tablet) {
